define('smarthome/models/thermostat', ['exports', 'ember-data'], function (exports, DS) {

  'use strict';

  exports['default'] = DS['default'].Model.extend({
    name: DS['default'].attr('string'),
    tempset: DS['default'].attr('number'),
    tempactual: DS['default'].attr('number'),
    hold: DS['default'].attr('boolean'),
    heat: DS['default'].attr('boolean')
  });

});