define('smarthome/models/lightswitch', ['exports', 'ember-data'], function (exports, DS) {

	'use strict';

	exports['default'] = DS['default'].Model.extend({
		name: DS['default'].attr('string'),
		on: DS['default'].attr('boolean'),
		level: DS['default'].attr('number'),
		isDimmer: (function () {
			return this.get('level') != null ? true : false;
		}).property('level')
	});

});