define('smarthome/router', ['exports', 'ember', 'smarthome/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('lighting');
    this.route('cameras');
    this.route('ltswitches');
    this.route('ltscenes');
    this.route('auth');
    this.route('gdopeners');
    this.route('thermostats');
  });

  exports['default'] = Router;

});